<template>
  <div class="song-detail-page" ref="page">

    <div class="player-disc-container">
      <div class="disc" ref="disc">
        <div class="disc-outer">
          <img v-if="currentSongDetail" class="disc-inner" :src="currentSongDetail.al.picUrl">
          <div v-else class="disc-inner"></div>
        </div>
      </div>
      <img class="player-bar" ref="playerBar" src="@/assets/image/player_bar.png">
    </div>

    <div class="playing-text">
      <span v-if="currentSongDetail" class="title">{{ currentSongDetail.name }}</span>
      <div class="lyric-container">
        <ul ref="lyricUl">
          <li v-for="(item,index) in lyrics" :key="item"
              :class="{'lyric-active' : index === lyricIndex}">
            <el-tooltip
                effect="light"
                :content="formatSongTime(item.t*1000)+'-'+item.c"
                placement="left"
                :open-delay="0"
            >
              {{ item.c === '' ? '...' : item.c }}
            </el-tooltip>
          </li>
        </ul>
      </div>
    </div>
    <div class="simi">
      <h3>相似歌曲</h3>
      <h4>。。。</h4>
      <h4>。。。</h4>
      <h4>。。。</h4>
    </div>
  </div>
</template>

<script>
import {watch, ref, onMounted, computed} from 'vue'
import {useStore} from 'vuex'
import axios from 'axios'

export default {
  name: "SongDetailPage",
  props: ['onshow'],
  setup(props) {
    const store = useStore();
    let currentSongDetail = computed(() => store.state.currentSongDetail)
    let currentTime = computed(() => store.state.currentTime)
    /*页面*/
    let page = ref(null)
    watch([props, currentSongDetail], ([p, csd]) => {
      // 一方面用户决定关闭歌曲详情页，另一方面没有歌曲了，就应该关闭该页
      if (p.onshow && csd) {
        page.value.style.height = 'calc(100% - 60px)'
      } else {
        page.value.style.height = '0'
      }
    })
    /*左边的磁盘动画*/
    let playerBar = ref(null), disc = ref(null)
    let paused = computed(() => store.state.paused)
    onMounted(() => {
      watch(paused, (val) => {
        if (!val) {
          disc.value.style.animationPlayState = "running"
          playerBar.value.style.transform = "translate(-20px) rotate(0)"
          playerBar.value.style.msTransform = "translate(-20px, 0) rotate(0)"
          playerBar.value.style.webkitTransform = "translate(-20px, 0) rotate(0)"
          playerBar.value.style.MozTransformStyle = "translate(-20px, 0) rotate(0)"
          playerBar.value.style.OTransform = "translate(-20px, 0) rotate(0)"
        } else {
          disc.value.style.animationPlayState = "paused"
          playerBar.value.style.transform = "translate(-20px) rotate(-45deg)"
          playerBar.value.style.msTransform = "translate(-20px, 0)  rotate(-45deg)"
          playerBar.value.style.webkitTransform = "translate(-20px, 0) rotate(-45deg)"
          playerBar.value.style.MozTransformStyle = "translate(-20px, 0) rotate(-45deg)"
          playerBar.value.style.OTransform = "translate(-20px, 0)  rotate(-45deg)"
        }
      }, {immediate: true})
    })

    /*歌词*/
    function generateLyricsArray(lrcsStr) {
      let lrcArr = []
      let lrcStrs = lrcsStr.split('\n')
      for (let i in lrcStrs) {
        lrcStrs[i] = lrcStrs[i].trim()
        let t = lrcStrs[i].match(/^\[\d+:[0-5][0-9]\.\d+.\]/)
        if (!t) break
        // t as ["[00:000]"]
        t = t[0]
        // t as "[00:000]"
        let s = t.substring(1, t.length - 1).split(':')
        lrcArr.push({
          t: parseFloat(s[0]) * 60 + parseFloat(s[1]),
          c: lrcStrs[i].substring(t.length)
        })
      }
      return lrcArr
    }

    function formatSongTime(n) {
      n = Math.floor(n / 1000)
      let min = (n / 60) >= 10 ? '' : '0' + Math.floor(n / 60);
      let sec = (n % 60 >= 10 ? '' : '0') + (n % 60);
      return min + ':' + sec
    }

    let lyrics = ref("暂无歌词")
    let lyricIndex = ref(0)
    let lyricUl = ref(null)
    watch(currentSongDetail, (val) => {
      if (val) {
        axios.get("/lyric", {
          params: {
            id: val.id,
          },
        }).then((res) => {
          lyrics.value = res.data.lrc.lyric;
          lyrics.value = generateLyricsArray(lyrics.value)
          // console.log(lyrics.value)
        });
      }
    }, {immediate: true})
    watch(currentTime, (val) => {
      if (lyrics.value.length < 1) {
        lyricIndex.value = 0
      }
      let i = 0, j = lyrics.value.length
      while (i < j) {
        let m = parseInt((i + j) / 2);
        if (lyrics.value[m].t >= val) {
          j = m
        } else {
          i = m + 1
        }
      }
      lyricIndex.value = i - 1
      if (lyricIndex.value > 4) {
        lyricUl.value.style.marginTop = (lyricIndex.value - 4) * (-30) + 'px'
      } else if (lyricIndex.value < 4) {
        lyricUl.value.style.marginTop = '0px'

      }
    })
    return {
      page, currentSongDetail,
      /*磁盘*/
      paused,
      playerBar,
      disc,
      /*歌词*/
      lyrics,
      lyricIndex,
      lyricUl,
      formatSongTime

    }
  }
}
</script>

<style lang="less" scoped>
/*页面容器*/
.song-detail-page {
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: 0;
  transition: all 0.5s;
  position: fixed;
  z-index: 3;
  bottom: 0;
  background: -webkit-linear-gradient(top, rgb(255, 255, 255), rgb(216, 216, 216)); /* Safari 5.1 - 6 */
  background: -o-linear-gradient(top, rgba(255, 255, 255, 1), rgb(216, 216, 216)); /* Opera 11.1 - 12*/
  background: -moz-linear-gradient(top, rgba(255, 255, 255, 1), rgb(216, 216, 216)); /* Firefox 3.6 - 15*/
  background: linear-gradient(to top, rgba(255, 255, 255, 1), rgb(216, 216, 216)); /* 标准的语法 */
}

@keyframes disc-rotate-animate {
  0% {
    transform: translate(-50%, 0) rotate(0deg);
  }
  100% {
    transform: translate(-50%, 0) rotate(360deg);
  }
}

.player-disc-container {
  margin-top: 30px;
  width: 30%;
  height: 400px;
  position: relative;

  .player-bar {
    position: absolute;
    left: 50%;
    top: 0;
    width: 92px;
    height: 128px;

    transition: all 1s;

    transform-origin: 12px 12px;
    transform: translate(-20px);
    -ms-transform-origin: 12px 12px; /* IE 9 */
    -webkit-transform-origin: 12px 12px; /* Safari 和 Chrome */
    -moz-transform-origin: 12px 12px; /* Firefox */
    -o-transform-origin: 12px 12px; /* Opera */
  }

  .disc {
    position: absolute;
    left: 50%;
    top: 75px;
    transform: translate(-50%, 0);
    animation: disc-rotate-animate 10s linear infinite;
    animation-play-state: paused;

    .disc-outer {
      position: relative;
      width: 240px;
      height: 240px;
      border: rgb(222, 222, 222) 10px solid;
      border-radius: 50%;
      box-sizing: border-box;
      background-color: black; /* 浏览器不支持的时候显示 */
      background-image: linear-gradient(to right, black, gray, black);

      /* 标准的语法（必须放在最后） */

      .disc-inner {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        width: 150px;
        height: 150px;
        border-radius: 50%;
        background-color: black;
      }

    }
  }
}

.playing-text {
  text-align: center;
  width: 30%;

  .title {
    display: block;
    margin: 20px auto 0;
    font-size: 24px;
    font-weight: normal;
    letter-spacing: 2px;
  }

  .lyric-container {
    border-top: #cccccc 2px solid;
    border-bottom: #cccccc 2px solid;
    list-style: none;
    margin-top: 40px;
    padding: 0;
    font-size: 18px;
    max-height: 270px;
    overflow: hidden;

    ul {
      margin: 0;
      padding: 0;
      margin-top: 0;
      transition: margin-top 0.5s;
    }

    li {
      display: block;
      line-height: 30px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .lyric-active {
      color: red;
      font-weight: bolder;
    }
  }

}

.simi {
  width: 30%;

  h4 {
    text-align: center;

  }
}
</style>
