<template>
  <el-container>
    <!-- 头部 -->
    <el-header>
      <img id="logo" src="@/assets/image/demo.svg" alt="logo">
      <span>athan</span>

      <el-icon id="back-btn" @click="$router.go(-1)">
        <arrow-left/>
      </el-icon>

      <div id="search-area">
        <el-input
            placeholder="请输入歌曲名或歌手名"
            size="small"
            v-model="searchInput"
            @input="debouncedShowMessage"
        />
        <el-icon id="search-icon">
          <search/>
        </el-icon>
      </div>

      <!--      <LoginBar style="position: absolute; right: 5%; top: 20%" ></LoginBar>-->
    </el-header>

    <el-container>
      <el-aside width="20%">
        <LeftNav></LeftNav>
      </el-aside>

      <el-main>
        <el-scrollbar height="100vh" :native="false">
          <router-view></router-view>
        </el-scrollbar>
      </el-main>

    </el-container>
    <PlayBar></PlayBar>

  </el-container>

</template>

<script>
import LeftNav from "@/components/LeftNav";
import PlayBar from "@/components/home/PlayBar";
import {ref, h} from 'vue'
import {ElMessage} from 'element-plus'

export default {
  name: "Home",
  components: {
    PlayBar,
    LeftNav,
  }, setup() {


    let searchInput = ref('')

    function showSuggestion(val) {
      if (val === '') return
      ElMessage({
        message: h('div', null, [
          h('span', {style: 'color: teal; font-size:12px'}, '暂未开发搜索功能，先演示个防抖吧：'),
          h('br'),
          h('i', {style: 'color: teal'}, val),
        ]),
        type: 'success',
        duration: 2000,
        showClose: true
      })
    }

    function debounce(func, delay) {
      let timer = null
      return function (...args) {
        if (timer) clearTimeout(timer)
        timer = setTimeout(() => {
          func.apply(this, args)
        }, delay)
      }
    }

    let debouncedShowMessage = debounce(showSuggestion, 500)


    return {
      searchInput,
      debouncedShowMessage
    }
  }
}
</script>

<style scoped>
.el-container {
  width: 100%;
}

.el-header {
  display: flex;
  align-items: center;
  position: relative;
  background: #c62f2f;
}

#logo {
  width: 40px;
  height: 40px;
  margin-left: 20px;
}

.el-header span {
  width: 100px;
  margin-left: 10px;
  font-weight: 500;
  font-size: 20px;
  color: #ffffff;
}

#back-btn {
  width: 25px;
  height: 25px;
  text-align: center;
  line-height: 25px;
  margin-left: 30px;
  color: #ffffff;
  background: rgba(77, 71, 71, 0.1);
  border-radius: 50%;
}

/*搜索区域*/
#search-area {
  margin-left: 50px;
  position: relative;
}

>>> .el-input__inner {
  opacity: 0.5;
  width: 225px;
  height: 25px;
  border-radius: 12px;
}

#search-icon {
  position: absolute;
  right: 5%;
  top: 20%;
  cursor: pointer;
  opacity: 0.5;
  color: aliceblue;
}

</style>