<template>
  <div class="play-bar">
    <audio
        autoplay
        :src="currentSongUrl"
        @timeupdate="onTimeUpdate"
        @pause="onPaused"
        @loadeddata="onLoadedData"
        @play="onPlay"
        @error="audioLoadError"
        @ended="playNextSong"
        ref="audio"
    ></audio>
    <!--歌曲信息-->
    <div v-if="currentSongDetail" class="song-info">
      <div class="song-info__page-onshow" ref="songInfoPageOnshow">
        <div class="iconfont-container" @click="showSongDetailPage=!showSongDetailPage">
          <i class="iconfont icon-jiantouxia"></i>
        </div>
      </div>
      <div class="song-info__page-hidden" ref="songInfoPageHidden">
        <div class="song-cover-container" @click="showSongDetailPage=!showSongDetailPage">
          <el-image class="song-cover" :src="currentSongDetail.al.picUrl"></el-image>
          <div class="cover-mask"><i class="iconfont icon-shangyi"/></div>
        </div>
        <div class="song-info-right">
          <div class="song-name">{{ currentSongDetail.name }}</div>
          <div class="song-author">
            <span v-for="(item, index) in currentSongDetail.ar" :key="item.id">
              {{ index == 0 ? item.name : '/' + item.name }}
            </span>
          </div>
        </div>
      </div>
    </div>
    <!--当前无歌曲的占位元素-->
    <div v-else style="width: 30%; height: 80%; "></div>

    <!--进度条和播放控制按钮-->
    <div class="process-box">
      <div class="controls">
        <div class="control-btn">
          <div @click="changePlayOrder">
            <div v-show="playOrderIndex==0">
              <el-tooltip effect="light" content="顺序播放" placement="top" :open-delay="500">
                <i class="iconfont icon-liebiaoshunxu"></i>
              </el-tooltip>
            </div>
            <div v-show="playOrderIndex==1">
              <el-tooltip effect="light" content="列表循环" placement="top" :open-delay="500">
                <i class="iconfont icon-liebiaoxunhuan"></i>
              </el-tooltip>
            </div>
            <div v-show="playOrderIndex==2">
              <el-tooltip effect="light" content="单曲循环" placement="top" :open-delay="500">
                <i class="iconfont icon-danquxunhuan"></i>
              </el-tooltip>
            </div>
            <div v-show="playOrderIndex==3">
              <el-tooltip effect="light" content="随机播放" placement="top" :open-delay="500">
                <i class="iconfont icon-suiji"></i>
              </el-tooltip>
            </div>
          </div>
        </div>
        <div class="control-btn" @click="clickPrevSong">
          <i class="iconfont icon-shangyishou"></i>
        </div>
        <div class="control-btn" @click="startOrPauseMusic">
          <div v-show="!paused">
            <i class="iconfont icon-zanting"></i>
          </div>
          <div v-show="paused">
            <i class="iconfont icon-bofang"></i>
          </div>
        </div>
        <div class="control-btn" @click="clickNextSong">
          <i class="iconfont icon-xiayishou"></i>
        </div>
        <div class="control-btn">
          <el-tooltip effect="light" content="歌词(暂未开发)" placement="top" :open-delay="500">
            <i class="iconfont icon-geciweidianji"></i>
          </el-tooltip>
        </div>
      </div>
      <div class="process-bar" style="color: gray; font-size: 16px">
        <!-- 播放进度条 -->
        <span class="process-time">
          {{ currentSongDetail ? formatSongTime(currentTime * 1000) : 0 }}</span>
        <el-slider
            class="playSlider"
            v-model="currentTime"
            size="small"
            :max="Math.floor(currentSongDetail?currentSongDetail.dt / 1000:0)"
            :show-tooltip="false"
            @change="updateCurrentTime"
        ></el-slider>
        <span class="process-time">
          {{ currentSongDetail ? formatSongTime(currentSongDetail.dt) : 0 }}
        </span>
      </div>
    </div>
    <div class="right-btns">
      <!--音量按钮-->
      <div class="volume-btn">
        <i @click="changeShowVolumeSlider" class="iconfont icon-yysound"></i>
        <div class="volume-slider" v-show="showVolumeSlider">
          <el-slider v-model="volume" vertical height="100px"/>
        </div>
      </div>
      <!--播放列表按钮-->
      <div class="play-list-btn">
        <i @click="changeShowPlayList" class="iconfont icon-danlieliebiao"></i>
      </div>
      <!--播放列表-->
      <div v-show="showPlayList" class="play-list">
        <div style="display: flex;justify-content: space-between; align-items: center;">
          <h3>当前播放</h3>
          <div style=" color:gray;font-size: 16px; margin-right: 10px; cursor: pointer"
               @click="clearPlayList">
            清除所有
          </div>
        </div>
        <div class="play-list-songs-container">
          <el-table :data="playList" :show-header="false"
                    :row-style="rowStyle"
                    @cell-dblclick="playSong"
                    max-height="300" :stripe="true" style="width: 100%">
            <el-table-column>
              <template #default="scope">
                {{ scope.row.name }}
              </template>
            </el-table-column>
            <el-table-column>
              <template #default="scope">
                <span v-for="(item,index) in scope.row.ar" :key="item.id">
                  {{ index == 0 ? item.name : '/' + item.name }}
                </span>
              </template>
            </el-table-column>
            <el-table-column width="60">
              <template #default="scope">
                {{ formatSongTime(scope.row.dt) }}
              </template>
            </el-table-column>
            <el-table-column width="40" align="right">
              <template #default="scope">
                <el-tooltip effect="light" content="从播放列表删除" placement="top" :open-delay="500">
                  <i class="iconfont icon-shanchu" @click="removeFromPlayList(scope.row)"></i>
                </el-tooltip>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
  <SongDetailPage :onshow="showSongDetailPage"></SongDetailPage>
</template>

<script>
import {computed, ref, watch, onMounted} from 'vue'
import {useStore} from 'vuex'
import {ElMessage} from 'element-plus'
import SongDetailPage from "@/components/home/SongDetailPage";


export default {
  name: "PlayBar",
  components: {SongDetailPage},
  setup() {

    const store = useStore();
    let currentSongDetail = computed(() => store.state.currentSongDetail)
    let currentSongUrl = computed(() => currentSongDetail.value ?
        `https://music.163.com/song/media/outer/url?id=${currentSongDetail.value.id}.mp3` : '')

    /*专辑封面*/
    let showSongDetailPage = ref(false)
    let songInfoPageHidden = ref(null), songInfoPageOnshow = ref(null)
    watch(showSongDetailPage, (val) => {
      if (val) {
        songInfoPageOnshow.value.style.marginTop = '0'
        songInfoPageHidden.value.style.marginTop = '48px'
      } else {
        songInfoPageOnshow.value.style.marginTop = '-48px'
        songInfoPageHidden.value.style.marginTop = '0'
      }

    })
    /*播放顺序*/
    let playOrders = ['normal', 'list-repeat', 'single-repeat', 'random']
    let playOrderIndex = ref(localStorage.getItem('playOrderIndex')
        ? parseInt(localStorage.getItem('playOrderIndex'))
        : 0)

    function changePlayOrder() {
      playOrderIndex.value = (playOrderIndex.value + 1) % playOrders.length
      localStorage.setItem('playOrderIndex', playOrderIndex.value)
    }

    /*音频播放*/
    const audio = ref(null)
    let currentTime = ref(0)
    let paused = ref(true)

    function onPlay() {
      store.commit('SET_PAUSED', false)
      paused.value = false
    }

    function onPaused() {
      store.commit('SET_PAUSED', true)
      if (playOrders[playOrderIndex.value] != 'single-repeat') {
        paused.value = true
      } else {
        audio.value.load()
      }
    }

    function onLoadedData() {
      audio.value.volume = volume.value / 100
      store.commit('SET_CURRENT_TIME', 0)
    }

    function startOrPauseMusic() {
      try {
        if (audio.value.paused) {
          audio.value.play()
        } else {
          audio.value.pause()
        }
      } catch (e) {
        showMessage('该歌曲暂时播放不了，请换一首试试吧', 'warning')
      }
    }

    function updateCurrentTime() {
      audio.value.currentTime = currentTime.value
    }

    function formatSongTime(n) {
      n = Math.floor(n / 1000)
      let min = (n / 60) >= 10 ? '' : '0' + Math.floor(n / 60);
      let sec = (n % 60 >= 10 ? '' : '0') + (n % 60);
      return min + ':' + sec
    }

    function onTimeUpdate() {
      currentTime.value = audio.value.currentTime
      store.commit('SET_CURRENT_TIME', audio.value.currentTime)
    }

    function showMessage(msg, type) {
      ElMessage({
        message: msg,
        type: type,
      })
    }

    function audioLoadError() {
      if (currentSongDetail.value) {
        showMessage('该歌曲暂时播放不了，请换一首试试吧', 'warning')
      }
    }

    /*音量控制*/
    let volume = ref(
        (localStorage.getItem('volume') ? parseInt(localStorage.getItem('volume')) : 100)
    )
    let showVolumeSlider = ref(false)
    watch(volume, (val) => {
      localStorage.setItem('volume', val)
      audio.value.volume = val / 100
    })


    function changeShowVolumeSlider() {
      showVolumeSlider.value = !showVolumeSlider.value
    }

    /*播放列表*/
    let showPlayList = ref(false)
    let playList = computed(() => store.state.playList)

    function changeShowPlayList() {
      showPlayList.value = !showPlayList.value
    }

    function rowStyle({row}) {
      if (currentSongDetail.value && row.id === currentSongDetail.value.id) {
        return 'color:red;'
      }
      return ''
    }

    function clearPlayList() {
      store.commit('CLEAR_PLAYLIST')
    }

    function playSong(row) {
      if (currentSongDetail.value != null && row.id == currentSongDetail.value.id) return;

      store.commit('DBCLICK_SONG', row)
    }

    function removeFromPlayList(row) {
      store.commit('REMOVE_FROM_PLAYLIST', row)
    }

    function clickPrevSong() {
      if (!playList.value || playList.value.length == 0) {
        showMessage('播放列表是空的，快选几首歌听听吧', 'success')
        return
      }

      if (playOrders[playOrderIndex.value] === 'random') {
        store.commit('SET_CURRENT_SONG_DETAIL', playList.value[randomInt(0, playList.value.length - 1)])
      } else {
        let currentSongIndex = -1;
        if (currentSongDetail.value) {
          currentSongIndex = playList.value.findIndex((item) => item.id == currentSongDetail.value.id)
        }
        store.commit('SET_CURRENT_SONG_DETAIL',
            (currentSongIndex - 1) < 0 ?
                playList.value[playList.value.length - 1] :
                playList.value[currentSongIndex - 1])
      }
    }

    function clickNextSong() {
      if (!playList.value || playList.value.length == 0) {
        showMessage('播放列表是空的，快选几首歌听听吧', 'success')
        return
      }
      if (playOrders[playOrderIndex.value] === 'random') {
        store.commit('SET_CURRENT_SONG_DETAIL', playList.value[randomInt(0, playList.value.length - 1)])
      } else {
        let currentSongIndex = -1;
        if (currentSongDetail.value) {
          currentSongIndex = playList.value.findIndex((item) => item.id == currentSongDetail.value.id)
        }
        store.commit('SET_CURRENT_SONG_DETAIL', playList.value[(currentSongIndex + 1) % playList.value.length])
      }
    }

    function randomInt(min, max) {
      return Math.floor(Math.random() * (max - min + 1)) + min
    }

    function playNextSong() {
      switch (playOrders[playOrderIndex.value]) {
        case "normal": {
          let currentSongIndex = -1;
          if (currentSongDetail.value) {
            currentSongIndex = playList.value.findIndex((item) => item.id == currentSongDetail.value.id)
          }
          if (currentSongIndex == -1) {
            store.commit('SET_CURRENT_SONG_DETAIL', playList.value[0])
          } else if (currentSongIndex < playList.value.length - 1) {
            store.commit('SET_CURRENT_SONG_DETAIL', playList.value[currentSongIndex + 1])
          } else {
            showMessage('已播放完播放列表', 'success')
          }
          break
        }
        case "list-repeat": {
          let currentSongIndex = -1
          if (currentSongDetail.value) {
            currentSongIndex = playList.value.findIndex((item) => item.id == currentSongDetail.value.id)
          }
          store.commit('SET_CURRENT_SONG_DETAIL', playList.value[(currentSongIndex + 1) % playList.value.length])
          break
        }

        case "single-repeat": {
          break
        }
        case "random": {
          store.commit('SET_CURRENT_SONG_DETAIL', playList.value[randomInt(0, playList.value.length - 1)])
          break
        }
      }
    }

    return {
      showSongDetailPage,
      songInfoPageHidden,
      songInfoPageOnshow,
      currentSongDetail,
      currentSongUrl,
      currentTime,
      startOrPauseMusic,
      audio,
      showMessage,
      onTimeUpdate,
      updateCurrentTime,
      formatSongTime,
      playOrderIndex,
      changePlayOrder,
      paused,
      onPlay,
      onPaused,
      onLoadedData,
      audioLoadError,
      volume,
      showVolumeSlider,
      changeShowVolumeSlider,
      showPlayList,
      changeShowPlayList,
      playList,
      rowStyle,
      playSong,
      playNextSong,
      removeFromPlayList,
      clearPlayList,
      clickNextSong,
      clickPrevSong
    }
  }
}
</script>

<style lang="less" scoped>
.play-bar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 60px;
  background-color: white;
  box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.3);
  z-index: 5;
}

/*歌曲信息--封面和作者信息*/
.song-info {
  width: 30%;
  height: 80%;
  position: relative;
  overflow: hidden;


  /*歌曲详情页没有打开*/

  .song-info__page-hidden {
    margin-top: 0px;
    transition: margin-top 1s;
    width: 100%;
    display: flex;
    align-items: center;
    /*封面*/

    .song-cover-container {
      position: relative;
      top: 0;

      .song-cover {
        vertical-align: middle;
        height: 48px;
        width: 48px;
        border-radius: 4px;
      }

      .cover-mask {
        background-color: rgba(0, 0, 0, .3);
        height: 48px;
        width: 48px;
        border-radius: 4px;
        position: absolute;
        top: 0;
        display: none;

        .iconfont {
          color: white;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
    }

    .song-cover-container:hover {
      .cover-mask {
        display: block;
      }
    }

    /*歌名作者信息*/

    .song-info-right {
      margin-left: 20px;
      width: 60%;
      font-size: 16px;
    }
  }

  /*歌曲详情页打开了*/

  .song-info__page-onshow {
    margin-top: -48px;
    transition: margin-top 1s;

    .iconfont-container {
      width: 48px;
      height: 48px;
    }

    .iconfont {
      display: block;
      margin: 0 auto;
      line-height: 48px;
    }
  }
}

.process-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: space-around;
  width: 40%;
  height: 100%;

  .controls {
    width: 80%;
    display: flex;
    justify-content: space-around;

    .control-btn {
      width: 30px;
      height: 30px;
    }
  }

  .process-bar {
    display: flex;
    width: 100%;
    height: 20%;
    margin-bottom: 10px;


    .process-time {
      margin: 0 2%;
    }
  }

}

.right-btns {
  display: flex;
  justify-content: space-around;
  width: 20%;
  height: 100%;
  position: relative;

  .volume-btn {
    width: 40px;
    position: relative;

    .volume-slider {
      display: flex;
      align-items: center;
      position: absolute;
      top: -110px;
    }
  }

  .play-list-btn {
    width: 40px;
  }

  .play-list {
    position: absolute;
    width: 400px;
    padding-left: 10px;
    background-color: white;
    top: 0;
    right: 0;
    box-shadow: grey 0px 0px 1px 0px;
    transform: translate(0, -100%);
    z-index: 4;
  }
}

/*图标*/
i.iconfont {
  font-size: 20px;
  width: 20px;
  margin: 2px auto
}

i.iconfont:hover {
  color: red;
}

/*el*/
.el-table tbody tr .iconfont {
  opacity: 0;
  transition: opacity 0.5s;
}

.el-table tbody tr:hover .iconfont {
  opacity: 1;
}

/deep/ .el-slider__button {
  height: 10px;
  width: 10px;
  border-color: red;
}

/deep/ .el-slider__bar {
  background-color: red;
}

.clearfix::after {
  content: '';
  display: block;
  height: 0;
  clear: both;
}
</style>